import React from 'react';
import PropTypes from 'prop-types';
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live';
import * as Hds from 'hds-react';
import theme from 'prism-react-renderer/themes/github'; // included by react-live

import './livecode.scss';

function clearSelection() {
  if (window.getSelection) {
    window.getSelection().removeAllRanges();
  } else if (document.selection) {
    document.selection.empty();
  }
}

function copy(textarea) {
  textarea.focus();
  textarea.select();

  try {
    document.execCommand('copy');
  } catch (err) {
    console.warn('Oops, unable to copy');
  }
  clearSelection();
}

const LiveCode = ({ code, className, ...rest }) => {
  const editorRef = React.useRef();
  const textAreaId = 'live-code-textarea';
  const matches = className.match(/language-(?<lang>.*)/);
  const title = matches?.groups?.lang === 'html' ? 'Core example' : 'Example';

  return (
    <LiveProvider code={code} scope={{ ...Hds }}>
      <div className="live-code">
        <div className="live-code-header">
          <p className="live-code-title">{title}</p>
          <Hds.Link
            href="https://hds.hel.fi/storybook/react/?path=/story/components-button--primary"
            size="m"
            className="live-code-external-link"
          >
            View in Storybook
          </Hds.Link>
        </div>
        <LivePreview className="live-code-preview" />
        <div ref={editorRef} className="live-code-content">
          <LiveEditor
            style={{ paddingBottom: '2rem', overflowWrap: 'break-word' }}
            textareaId={textAreaId}
            theme={theme}
          />
          <Hds.Button
            className="live-code-content-button"
            variant="secondary"
            size="small"
            onClick={() => {
              if (editorRef.current) {
                const textarea = editorRef.current.querySelector(`#${textAreaId}`);
                copy(textarea);
              }
            }}
          >
            Copy code
          </Hds.Button>
        </div>
        <LiveError />
      </div>
    </LiveProvider>
  );
};

LiveCode.propTypes = {
  code: PropTypes.string.isRequired,
};

export default LiveCode;
